@import "scss/_variables";
@import "scss/_mixins";

.consumption-collapse {
  :global {
    .consumption-collapse {
      margin: 24px 0;
      position: relative;
      background-color: $white-color;
      padding: 24px;

      @include breakpoint(size-lg-up) {
        margin: 40px 0;
      }

      .ant-collapse-item {
        border: 0;
        &.ant-collapse-item-active {
          @include box-shadow;
        }

        .ant-collapse-header {
          padding: 0;

          h1.ant-typography {
            font-size: 32px;
            background-repeat: no-repeat;
            background-position: left center;
            background-size: 65px auto;

            span.ant-typography {
              color: $third-color;
            }

            @include breakpoint(size-lg-down) {
              font-size: 16px;
              background-size: 43px auto;
              padding: 5px 50px !important;

              span.ant-typography {
                display: block;
              }
            }
          }

          h4.ant-typography {
            @include title-14-primary;
            background-color: $bg-gray;
            padding: 7px 8px 6px;
            border-radius: 15px;

            span.anticon {
              font-size: 14px;
            }

            @include breakpoint(size-lg-up) {
              padding: 7px 14px 6px;

              span.anticon {
                margin-right: 5px;
              }
            }
          }
        }

        .ant-collapse-content {
          overflow: visible;
          padding: 0;

          .ant-collapse-content-box {
            padding: 0;

            .ant-tabs {
              overflow: visible;
  
              .ant-tabs-nav {
                margin-left: -24px;
                margin-right: -24px;
  
                &:before {
                  border-color: $gray-normal-color;
                  bottom: 1px;
                }
              }
            }

            .download-cons-btn {
              margin-bottom: 24px;

              @include breakpoint(size-lg-up) {
                text-align: right;
              }
            }

            .app-table {
              .ant-table-tbody {
                .ant-table-cell {
                  .ant-row {
                    h3.ant-typography {
                      @include title-12;
                      margin-right: 5px;
                    }

                    h2.ant-typography {
                      @include title-14-primary;
                      margin: 0;

                      span.ant-typography {
                        font-weight: normal;
                      }
                    }
                  }
                }
              }

              &.light-table {
                .ant-table-tbody {
                  .ant-table-cell {
                    padding: 5px 0;
                  }
                }
              }
            }
          }
        }
      }

      &.consumption--light {
        .ant-collapse-header {
          h1.ant-typography {
            background-image: url(../../../assets/icons/light-icon.svg);
            padding: 15px 80px;
          }
        }
      }

      &.consumption--gas {
        .ant-collapse-header {
          h1.ant-typography {
            background-image: url(../../../assets/icons/gas-icon.svg);
            padding: 15px 80px;
          }
        }
      }
    }
  }
}
